













































































import uniSegmentedControl from '../../../components/uni-segmented-control.vue';
export default {
	components: {
		uniSegmentedControl
	},
	data() {
		return {
			items: [
				'微信支付',
				'支付宝',
			],
			vipInfo:{
				nickname:'',
				viptime:'',
				viptq:'',
				vippayff:'第一步:长按或截图二维码图片进行保存;<br />第二步:打开支付宝或微信，点开扫一扫;<br />第三步:点右上角从相册中选择刚保存的二维码;<br />第四步:输入购买会员相应金额;<br />第五步:支付时添加备注，备注会员手机号;<br />第六步:支付完成后，等待系统充值;',
				vipmoney:[
					{
						title:'',
						val:'',
						gqval:''
					},
					{
						title:'',
						val:'',
						gqval:''
					},
					{
						title:'',
						val:'',
						gqval:''
					}
				],
				alipay:'',
				weixin:''
			},
			current: 0,
			activeColor: '#fe5950',
			styleType: 'text',
			vipMoney:0
		}
	},
	onLoad() {
		
	},
	onShow() {
		this.getVipInfo();
	},
	methods: {
		getVipInfo(){
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			try {
				this.$http.post(this.websiteUrl+'/api/api/getVipInfo',{openid,sessionKey},(res) => {
					if(res.data.code==1){
						this.vipInfo = res.data.result;
					}else{
						uni.showModal({
							content:res.data.msg,
							showCancel:false
						})
					}
				});
			} catch (e) {
				uni.showModal({
					content:'网络异常,请稍后重试...',
					showCancel:false
				})
			}
		},
		onClickNum(num){
			this.vipMoney = num;
		},
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index;
			}
		}
	}
}
